import { Injectable } from '@angular/core';
import {Observable} from 'rxjs/Observable';
import {Http,Response,Headers} from '@angular/http';
import 'rxjs/add/operator/map';

@Injectable()
export class MovieService {

    private MaNhom:string = 'GP07';
    //Link tr n api backend ly danh sch phim
    private apiUrlGetMovie = 'http://svcy.myclass.vn/api/movie/getmovie'; 
   
    //khi to 1 i tng http  thc hin cc phng thc post, get v server
    constructor(private _http:Http) { }

    //Gi service ly danh sch phim t server backend v
    public LayDanhSachPhim():Observable<any[]> {

      //get(): goi phng thc get gi n server yu cu ly d liu.
      /*map():  a kiu d liu 1 chui v dng i tng response => t reponse ta c th parse ra
              kiu d liu json */
              let obServe:Observable<any>;
        obServe = this._http.get(this.apiUrlGetMovie).map((result:Response) => result.json());

        return obServe;

      
    }
  


    //Link tr n api backend ly thng tin phim, lch chiu, ...
    private apiUrlGetMovieDetail = 'http://svcy.myclass.vn/api/movie/GetMovieDetail';
    //Ly thng tin chi tit ca 1 phim da trn m phim 
    public LayChiTietPhim_LichChieu(MaPhim:number):any {
      //map():  a kiu d liu 1 chui v dng i tng response => t reponse ta c th parse ra kiu d liu json
      let obServe:Observable<any> = this._http.get(`${this.apiUrlGetMovieDetail}?id=${MaPhim}`).map((result:Response) => result.json());
      return obServe;
    }


  

    //Link tr n api backend ly thng tin phim, lch chiu, ... theo nhm
    private apiUrlGetMovieDetailByGroup = 'http://svcy.myclass.vn/api/movie/GetMovieDetailByGroup';
    //Ly thng tin chi tit ca 1 phim da trn m phim v m nhm
    public LayChiTietPhim_LichChieuTheoNhom(MaPhim:number,MaNhom:string):any {
         //map():  a kiu d liu 1 chui v dng i tng response => t reponse ta c th parse ra kiu d liu json
         let obServe:Observable<any> = this._http.get(`${this.apiUrlGetMovieDetailByGroup}?id=${MaPhim}&groupID=${MaNhom}`)
         .map((result:Response) => result.json());
         return obServe;
    }

    
    private apiLoadGhe =`http://svcy.myclass.vn/api/movie/GetCinimaRoomDetail`
    public LayDanhSachGhe(ShowTimeID:number):Observable<any[]> {
      let obServe:Observable<any> = this._http.get(`${this.apiLoadGhe}?ShowTimeID=${ShowTimeID}`).map((result:Response) => result.json());
      return obServe;
    }
      
  



     //Link tr n api backend post thng tin danh sch gh ngi  t , ... theo nhm, theo ngi dng
     private apiUrlPostBookingTickets = 'http://svcy.myclass.vn/api/movie/BookingTickets';  
     public DatVe(lstVe:any):Observable<any> {
  
      // post c json ln server phi c header v body : ty backend quy nh khc nhau
      let header = new Headers();
      header.append('Content-Type', 'application/x-www-form-urlencoded; charset=UTF-8');
      //Body l ni dung tham s gi ln server  y l chui json vi tn tham s l data (server quy nh)
      // lstVe = {ShowTimeID:3,Tickets:[{SeatID:'GP01_Rap01_A02',Price:85},{SeatID:'GP01_Rap01_A03',Price:85}],UserID:'khai123',GroupID:'GP01'};
    
      let body = `data=${JSON.stringify(lstVe)}`;
      var obServe = this._http.post(this.apiUrlPostBookingTickets, body, { headers: header }).map((result: Response) => result.json());
      return obServe;
     }


     public SearchPhim(dsphim,tenphim){
      let dsphimsearch = [];
      if(tenphim == ""){
        let mangRong = []
        return mangRong;
      }
      else {
        for(let i = 0; i < dsphim.length; i++){
          if(dsphim[i].Title.toLowerCase().indexOf(tenphim.toLowerCase()) > -1) {
            dsphimsearch.push(dsphim[i]);
          } 
        }
        return dsphimsearch;
      }
    }
  }
  


